.header {
  width: 100%;
  background-color: #1c1c21;
}

.container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  margin: 0;
  width: 61.5%;
}

.navigation {
  width: 1280px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0;
}

.menuList {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
}

.link {
  display: flex;
  justify-content: flex-start;
  color: #8585ad;
  text-decoration: none;
  transition: color 0.4s linear;
  cursor: pointer;
}

.link:hover {
  color: #f2f2f3;
}

.link_active {
  color: #f2f2f3;
}
